<template>
    <div>
      <nav-header></nav-header>
      <div class="nav-breadcrumb-wrap">
        <div class="container">
          <nav class="nav-breadcrumb">
            <a href="/">Home{{count}}{{userName}}</a>
            <span @click="add()">Goods</span>
          </nav>
        </div>
      </div>
      <div class="accessory-result-page accessory-page">
        <div class="container">
          <div class="filter-nav">
            <span class="sortby">Sort by:</span>
            <a href="javascript:void(0)" class="default cur" @click="sortGoods('default')">Default</a>
            <a href="javascript:void(0)" class="price" @click="sortGoods('price')">Price <svg class="icon icon-arrow-short" :class="{'sort-up':sortFlag==-1,'sort-down':sortFlag==1}"><use xlink:href="#icon-arrow-short"></use></svg></a>
            <a href="javascript:void(0)" class="filterby stopPop" @click="filterPrice()">Filter by</a>
          </div>
          <div class="accessory-result">
            <!-- filter -->
            <div class="filter stopPop" id="filter" v-bind:class="{'sortFilter':priceFilter}">
              <dl class="filter-price">
                <dt>Price:</dt>
                <dd><a href="javascript:void(0)" @click="priceType('all')">All</a></dd>
                <dd>
                  <a href="javascript:void(0)" @click="priceType(0)">0 - 100</a>
                </dd>
                <dd>
                  <a href="javascript:void(0)" @click="priceType(1)">100 - 500</a>
                </dd>
                <dd>
                  <a href="javascript:void(0)" @click="priceType(2)">500 - 1000</a>
                </dd>
                <dd>
                  <a href="javascript:void(0)" @click="priceType(3)">1000 - 5000</a>
                </dd>
              </dl>
            </div>

            <!-- search result accessories list -->
            <div class="accessory-list-wrap">
              <div class="accessory-list col-4">
                <ul>
                  <li v-for="item in List">
                    <div class="pic">
                      <a href="#"><img src="static/1.jpg" alt=""></a>
                    </div>
                    <div class="main">
                      <div class="name">{{item.productName}}</div>
                      <div class="price">{{item.salePrice}}</div>
                      <div class="btn-area">
                        <a href="javascript:;" class="btn btn--m" @click="addCart(item.productId)">加入购物车</a>
                      </div>
                    </div>
                  </li>

                </ul>
                <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="50">
                  加载中...
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <footer class="footer">
        <div class="footer__wrap">
          <div class="footer__secondary">
            <div class="footer__inner">
              <div class="footer__region">
                <span>Region</span>
                <select class="footer__region__select">
                  <option value="en-US">USA</option>
                  <option value="zh-CN">China</option>
                  <option value="in">India</option>
                </select>
              </div>
              <div class="footer__secondary__nav">
                <span>Copyright © 2017 IMooc All Rights Reserved.</span>
                <a href="http://us.lemall.com/us/aboutUs.html">
                  About Us
                </a>
                <a href="http://us.lemall.com/us/termsofUse.html">
                  Terms &amp; Conditions
                </a>
                <a href="http://us.lemall.com/us/privacyPolicy.html">
                  Privacy Policy
                </a>
              </div>
            </div>
          </div>
        </div>
      </footer>
      <modal v-bind:msgShow="msgShow" @close="closeModal">
        <p slot="message">
          请先登录，否则无法加入到购物车中
        </p>
        <div slot="btnGroup">
          <a class="btn btn-m" @click="msgShow=false">关闭</a>
        </div>
      </modal>
      <modal v-bind:msgShow="msgShowCart" @close="closeModal">
        <p slot="message">
          <svg class="icon-status-ok">
            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#icon-status-ok"></use>
          </svg>
          <span>加入购物车成功</span>
        </p>
        <div slot="btnGroup">
          <a class="btn btn-m" @click="msgShowCart=false">继续购物</a>
          <router-link  class="btn btn-m" href="javascript:;" to="/cart">查看购物车</router-link>
        </div>
      </modal>
    </div>
</template>
<script>
import axios from "axios"
import NavHeader from "./../components/NavHeader"

import Modal from "./../components/Modal"
export default {
  name: 'GoodsList',
  components:{NavHeader,Modal},
  data () {
    return {
      sortFlag:-1,
      List:[],
      busy: true,
      page:1,
      pageSize:5,
      priceFilter:false,
      priceChecked:'all',
      msgShow:false,
      msgShowCart:false
    }
  },
  computed:{
    count(){
      return this.$store.state.count
    },
    userName(){
      return this.$store.getters.userName
    }
  },
  mounted(){
    this.getGoodsList()
  },
  methods:{
    getGoodsList(flag){
      var param={
        page:this.page,
        pageSize:this.pageSize,
        sort:this.sortFlag,
        priceLevel:this.priceChecked
      }
      axios.get("/goods/goods.do",{params:param}).then((res)=>{
      debugger;
      if(flag==true){
        this.List=this.List.concat(res.data.result.list);
        if(res.data.result.count==0 || res.data.result.count<this.pageSize){
          this.busy=true;
        }else
        this.busy=false;
      }else{
        this.List=res.data.result.list;
        this.busy=false;
      }


    }).catch((res)=>{
      console.log(res);//发生错误时执行的代码
    });
    },
    sortGoods(type){
      if(type=='price'){
        if(this.sortFlag==-1){
          this.sortFlag=1;
        }else{
          this.sortFlag=-1
        }
        this.getGoodsList();
      }else{
        this.sortFlag=-1
        this.getGoodsList();
      }
    },
    priceType(num){
      this.busy=false;
      this.priceChecked=num;
      this.page=1;
      this.getGoodsList();
      this.priceFilter=false;
    },
    loadMore(){
      debugger
        this.busy=true;
          this.page++;
          this.getGoodsList(true)
    },
    filterPrice(){
      this.priceFilter=!this.priceFilter;
    },
    addCart(productId){
      axios.post("/goods/addCart.do",{productId:productId}).then((res)=>{
        debugger;
          if(res.data.status=='0'){
            this.msgShowCart=true;
            this.$store.commit('updateCartCount',1);
          }else{
            this.msgShow=true;
            // alert("加入失败"+res.data.msg)
          }
      })
    },
    closeModal(){
      // this.msgShow=false;
      // this.msgShowCart=false;
    },
    add(){
      // this.$store.commit("increment");
      this.$store.dispatch('incrementAction');
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .accessory-result{
    position:relative;
  }

  .sortFilter{
    right:0;
  }
  .sort-up{
    transform: rotate(180deg);
    -webkit-transition: all .3s ease-out;
    -moz-transition: all .3s ease-out;
    -ms-transition: all .3s ease-out;
    -o-transition: all .3s ease-out;
    transition: all .3s ease-out;
  }
  .sort-down{
    transform: rotate(0deg);
    -webkit-transition: all .3s ease-out;
    -moz-transition: all .3s ease-out;
    -ms-transition: all .3s ease-out;
    -o-transition: all .3s ease-out;
    transition: all .3s ease-out;
  }
</style>



