import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import GoodsList from '@/views/GoodsList'
import Title from '@/views/Title'
import Image from '@/views/Image'
import Cart from '@/views/Cart'
import address from '@/views/Address'

import showMe from '@/views/showMe/ShowMe'
Vue.use(Router)

export default new Router({
	// mode:'history',
  routes: [
  	{
      path: '/',
      name: 'GoodsList',
      redirect:'/showMe'
  	},
    {
      path: '/showMe',
      name: 'showMe',
      component: showMe,
    },
    {
      path: '/goods',
      name: 'GoodsList',
      component: GoodsList,
      children: [
      	{
      		path:'title',
      		name:'title',
      		component: Title,
      	},
      	{
      		path:'image',
      		name:'image',
      		component: Image,
      	}
      ]
    },
    // {
    //   path: '/cart/:goodsId',
    //   name: 'Cart',
    //   component: Cart,
  	// },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart,
    },
    {
      path: '/address',
      name: 'Address',
      component: address,
    }
  ]
})



